import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import useDarkMode from 'use-dark-mode';
import { Helmet } from 'react-helmet';
import AppContext from './AppContext';
import MainApp from './MainApp';
import GlobalStyles from './theme/GlobalStyles';
import { lightTheme, darkTheme } from './theme/themes';

function App() {
  window.matchMedia = null;
  const darkMode = useDarkMode(true);

  return (
    <AppContext.Provider value={{ darkMode }}>
      <ThemeProvider theme={darkMode.value ? darkTheme : lightTheme}>
        <GlobalStyles />
        <div className="App">
          <Helmet>
            <title>محمد عبد المنعم لتصميم المواقع و التطبيقات</title>
            <meta name="description" content="" />
            <meta name="keywords" content="المبرمج محمد عبد المنعم" />
            <meta name="author" content="المبرمج محمد عبد المنعم" />
            <meta property="og:title" content="القرآن الكريم كاملاً من المصحف المدني " />
            <meta property="og:description" content="قراءة القرآن الكريم كاملاً من المصحف المدني " />
            <meta property="og:image" content="https://mohamed-moneim.github.io/mmoneim9/images/mmoneim9.jpg" />
            <meta property="og:url" content="https://mohamed-moneim.github.io/mmoneim9" />
            <meta name="twitter:title" content=">القرآن الكريم كاملاً من المصحف المدني" />
            <meta name="twitter:description" content="قراءة المصحف الشريف كاملاً " />
            <meta name="twitter:image" content="https://mohamed-moneim.github.io/mmoneim9/images/mmoneim9.jpg" />
            <meta name="twitter:card" content="https://mohamed-moneim.github.io/mmoneim9/images/mmoneim9.jpg" />
          </Helmet>
          <BrowserRouter>
            <MainApp />
          </BrowserRouter>
        </div>
      </ThemeProvider>
    </AppContext.Provider>
  );
}

export default App;
